import { Region } from './../../providers/region/region.model';
import { RegionProvider } from './../../providers/region/region';
import { Category } from './../../providers/category/category.model';
import { CategoryProvider } from './../../providers/category/category';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Events } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-explorar',
  templateUrl: 'explorar.html',
  providers: [ CategoryProvider ]
})
export class ExplorarPage {

  // Array grande
  public categoriesBig:Category[] = [];

  // Array menor
  public categoriesSmall:Category[] = [];

  /**
   * Regioes do sistema
   * 
   */
  public regions: Region[] = [];

  /**
   * MÃ©todo constructor
   * 
   * @param navCtrl 
   * @param navParams 
   * @param modalCtrl 
   * @param categoryProvider 
   */
  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public modalCtrl: ModalController,
              public regionsProvider: RegionProvider,
              public categoryProvider: CategoryProvider,
              public events: Events) {}

  /**
   * Quando a view entrar
   * 
   */
  async ionViewDidEnter(){

    // Pega todas as categorias
    const categories = this.categoryProvider.listFromStorage();
    this.regions     = this.regionsProvider.listFromStorage();

    // Verifica se existem categorias pequenas
    if( categories.length <= 2 ) {
      this.categoriesBig = categories;
    } else {
      this.categoriesBig = categories.slice( 0, 2 );
      this.categoriesSmall = categories.slice( 2, categories.length );
    }
  }

  /**
   * pesquisar
   */
  public pesquisar() {
    const modal = this.modalCtrl.create( "PesquisarPage" );
    modal.present();
  }

  /**
   * click
   */
  public openList( category ) {
    this.navCtrl.push( 'VeiculosPage', { category } );
  }

  /**
   * Seta a regiao ativa
   * 
   * @param r 
   */
  public setRegion( r ) {

    // Seta a regiao no localstorage
    this.regionsProvider.setActivedRegion( this.regions[r].sigla );

    // Retira o actived de todos os outros itens
    this.regions = this.regions.map( item => {
      item.actived = false;
      return item;
    })

    // Seta o actived do item atual
    this.regions[r].actived = true;
  }

  /**
   * Abre o modal de pesquisa
   * 
   */
  search() {
    const modal = this.modalCtrl.create( 'PesquisarVeiculoPage' );
    modal.present();
  }
}

